
//
function InitValidator(write) {

	//
	$.validator.addMethod("demostic", function (value, element) {

		value = value.replace(/^\s+|\s+$/g, '');
		var	demostic = /^([\u4e00-\u9fa5a-zA-Z]+)$/;

		return this.optional(element) || (demostic.test(value));

	}, "");


	//
	$.validator.addMethod("subRequired", function(value, element){
		
		value = value.replace(/^\s+|\s+$/g, "");
		var holder = $(element).attr("placeholder");
		
		if( value != "" && value != holder )	return true;
		
		var inputs = $(element).closest(".guest").find("input");
		
		for( var i = 0; i < inputs.length; i++ ){
			
			var o = inputs[i];
			
			var n_value = o.value.replace( $(o).attr("placeholder"), "" )
								 .replace(/^\s+|\s+$/g, "");
			
			if( n_value != "" )
				return false;
			
		}
		
		return true;
		
	}, "");


	var o = {
		rules: {
			surname: {
				required: true,
				subRequired: true,
			},
			aftername: {
				required: true,
				subRequired: true,
			},
			surname2: {
				subRequired: true,
			},
			aftername2: {
				subRequired: true,
			},
			voucherEmail: {
				email: true
			},
			voucherFax: {
				number: true,
				rangelength: [6, 14]
			},
			voucherMobile: {
				number: true,
				rangelength: [6, 14]
			}
		},
		messages: {
			voucherEmail: {
				email: ''
			},
			voucherFax: {
				number: '',
				rangelength: '6-14'
			},
			voucherMobile: {
				number: '',
				rangelength: '614',
			}
		}
	};


	if (write.content.staticInfo.country == 70007) {	//
		
		var o1 = {
			rules: {
				surname: {
					demostic: true,
				},
				aftername: {
					demostic: true
				},
				surname2: {
					demostic: true,
				},
				aftername2: {
					demostic: true,
				},
			},
		};

		$.extend(true, o, o1);

	} else {		//

		var o1 = {
			rules: {
				surname: {
					letter: true
				},
				aftername: {
					letter: true
				},
				nationality: {
					required: true,
				},
				surname2: {
					letter: true
				},
				aftername2: {
					letter: true
				},
				nationality2: {
					subRequired: true,
				},
			},
		};

		$.extend(true, o, o1);

	}

	$("#orderForm").validate(o);
}

module.exports = InitValidator;



